@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,400i,500);
body {
  overflow-x: hidden;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}

/* Toggle Styles */

#viewport {
  padding-left: 250px;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

#content {
  width: calc(100% - 250px);
  position: absolute;
  margin-right: 0;
  padding: 2em .5em;
}

/* Sidebar Styles */

#sidebar {
  z-index: 1000;
  position: fixed;
  left: 250px;
  width: 250px;
  height: 100%;
  margin-left: -250px;
  overflow-y: auto;
  background: #37474F;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

#sidebar header {
  background-color: #263238;
  font-size: 20px;
  line-height: 52px;
  text-align: center;
}

#sidebar header a {
  color: #fff;
  display: block;
  text-decoration: none;
}

#sidebar header a:hover {
  color: #fff;
}

#sidebar .nav{
  
}

#sidebar .nav a{
  background: none;
  border-bottom: 1px solid #455A64;
  color: #CFD8DC;
  font-size: 14px;
  padding: 16px 24px;
}

#sidebar .nav a:hover{
  background: none;
  color: #ECEFF1;
}

#sidebar .nav a i{
  margin-right: 16px;
}

.form-control{
    font-size: .9rem !important;
}
h1{
    font-size: 1.3rem !important;
    margin-bottom: 50px !important;
}
h3{
  font-size: 1.1rem !important;
}
p{
  font-size: .8rem;
}
table{
    font-size: .9em !important;
}

.btn-primary{
    font-size: .9rem !important;
    background-color: #263238 !important;
}
.messages{
  list-style-type: none;
}
.message{
  margin: 15px 0;
  padding: 1rem;
  border-radius: 5px;
  position: relative;
  background-color: #e8e4dd;
  box-shadow: 0px 1px 3px 0px  rgba(153, 145, 153, 0.66);
}
.cancel-delete-btn{
  position: absolute;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: #263238d1;
  color:#fff;
  border-radius: 7px;
  bottom: 5px;
  right: 4px;
}
/* .compose-new{
 color: white;
 background-color: #263238d1;
} */
.cancel-delete-btn:hover{
  background-color: #263238d1;
}
.message .zmdi:hover{
  background-color: #263238d1;
}
.message .zmdi{
  color: #fff;
  font-size: 1.1rem;
  padding: .5rem .9rem;
  cursor: pointer;
  
}
.zmdi-edit{
  border-radius: 7px 0 0 7px;
}
.zmdi-delete{
  border-radius: 0 7px 7px 0;
}
.tooltip-arrow,
.white-tooltip + .tooltip > .tooltip-inner {background-color: #fff;}

.page-enter {
  opacity: 0;
}
.page-enter-active {
  opacity: 1;
  -webkit-transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
}
.page-exit {
  opacity: 1;
}
.page-exit-active {
  opacity: 0;
  -webkit-transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
}
